import React from 'react'
import './navbar.scss'
import { Link, useNavigate } from 'react-router-dom';
import MenuIcon from '@mui/icons-material/Menu';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import PermIdentityOutlinedIcon from '@mui/icons-material/PermIdentityOutlined';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import HomeIcon from '@mui/icons-material/Home';
import SearchRoundedIcon from '@mui/icons-material/SearchRounded';

const Navbar = () => {

    const goTo=useNavigate()

const toSearch=()=>{
    goTo('/search')
}

    return (

        <>
            <nav>
                <div className="navbar">
                    <div className="header">
                        <img src="https://cdn3.mydukaan.io/app/image/200x200/?url=https://dukaan-us.s3.amazonaws.com/5075066/3a0be459-476d-418e-8fe5-ec1d1ea8ef26/colourful-geometric-dia-de-los-muertos-instagram-greeting-post-221-5bfb4f20-8b57-4511-ac6c-5371453e76e6.png/91decac4-3cf1-4592-a0a2-2598470d1376.png" alt="" />
                    </div>
                    <div className="tools">
                        <ul className='tool'>
                            <li className='Home'>Home</li>
                            <li className='Category'>
                                <details>
                                    <summary>Category</summary>
                                </details>
                            </li>
                            <li className='About'>About us</li>
                            <li className='Privacy'>Privacy Policy</li>
                            <li className='Replacement'>Replacement Policy</li>
                        </ul>
                        <div className="menu">
                            <MenuIcon className='icon' />
                        </div>
                    </div>

                    <div className="search">
                        <Link to='/search'><SearchRoundedIcon className='s-icon' /></Link>
                    </div>
                    <div className="icons">
                        <ul>
                            <li className='searchs' ><Link to="/search"><SearchRoundedIcon className='iconss' /></Link></li>
                            <li className='home'><Link to="/"><HomeIcon className='icon' /></Link></li>
                            <li className='heart'><FavoriteBorderIcon className='icon' /></li>
                            <li className='cart'><ShoppingCartOutlinedIcon className='icon' /></li>
                            <li className='user'><PermIdentityOutlinedIcon  className='icon' /></li>
                        </ul>
                    </div>
                </div>
            </nav>
        </>
    )
}

export default Navbar