import React, { useState } from 'react'
import Navbar from './Navbar'
import Similar from './Similar'
import HeroSlider, { Slide } from 'hero-slider'
import image from './slideImgApi'
import "./home.scss"
import Background from 'hero-slider/dist/components/Slide/Background'

const Home = () => {
  // const[image,setImage]=useState();
  const [images, setImages] = useState(image);

  return (
    <>

      <Navbar />

      <div className="slides">

        <HeroSlider
          className='heroslider'
            height='40vh'
          settings={{
            
          }}
          
           >

          {
            images.map((i) => {
              const img = i.image;
              return (
                <>
                  {/* <Slide className='slide'
                  background={{
                    backgroundImageSrc: img,
                  }}
                
                /> */}
                  <Slide className='slide' >
                    <div className="image">
                      <img src={i.image} alt="" />
                    </div>
                  </Slide>
                </>

              )
            })
          }
        </HeroSlider>
      </div>

      <div className="similer-box">
        <Similar category={"Fiction"} title={'Fiction'} />
        <Similar category={"Young Adult"} title={'Young Adult'} />
        <Similar category={"Fantasy"} title={'Fantasy'} />
        <Similar category={"Mystery"} title={'Mystery'} />
      </div>
    </>
  )
}

export default Home