import React, { useEffect, useState } from 'react'
import { useNavigate, Link } from 'react-router-dom'
import books from './BookAPI';
import "./search.css"
import Navbar from './Navbar';
import HomeIcon from '@mui/icons-material/Home';
import SearchRoundedIcon from '@mui/icons-material/SearchRounded';


const Search = () => {

  const [bookData, setBookData] = useState(books)
  const [searchData, setSearchData] = useState(bookData);

  const navigate = useNavigate()

  const gOToAbout = (cur: any) => {
    navigate('/details', { state: { id: cur } })
    window.scrollTo({
      top: 0,
    });
  }

  const handelChange = (e: any) => {
    const searchItem = bookData.filter((f) => {
      return f.title.toLowerCase().includes((e.target.value).toLowerCase())
      // console.log(f.title)
    })
    setSearchData(searchItem);
    console.log(e.target.value);

  }


  return (
    <>
      {/* <Navbar/> */}
      <div className="search-home">

        <div className="search-bar">
          <div className="search-box">
            <div className="heading">
              <span className='span'>Search</span>
            </div>
            <form className="search" >
              <input className='input'
                type="text"
                onChange={handelChange}
                placeholder='Enter Book Name'
              />
              <SearchRoundedIcon className='search-icon' />
            </form>
            <div className="home-icon">
              <Link to='/'>< HomeIcon className='home' /></Link>
            </div>
          </div>
        </div>


        <div className="home">
          <div className="products">
            {
              searchData.map((e) => {
                const curprice = (e.price - (e.price * e.discount / 100)).toFixed(1);

                return (
                  <>
                    <div className="product" onClick={() => gOToAbout(e.key)} >
                      <div   >
                        <div className="image">
                          <img src={e.image_link} alt="image" />
                        </div>
                        <div className="details">
                          <h2 className='title'>{e.title}</h2>
                          <div className="price-box">
                            <span className="cur-price">{curprice}</span>
                            <span className="total-price">{e.price}</span>
                            <div className="discount">( {e.discount}% OFF )</div>
                          </div>
                        </div>
                      </div>
                      <div className="button">ADD TO CARD</div>
                    </div>

                  </>
                )
              })
            }
          </div>
        </div>
      </div>

    </>


    //  <div onClick={GOToAbout}>Home</div>


  )
}

export default Search;